@import url("https://fonts.googleapis.com/css2?family=Overpass:wght@400;700&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  width: 100%;
  min-height: 100vh;
  background-color: #121417;
  color: white;
  display: grid;
  place-items: center;
  font-family: "Overpass", sans-serif;
  font-size: 15px;
}

main {
  width: min(100% - 4rem, 26.25rem);
  background-color: #252d37;
  border-radius: 1.3rem;
}

.wrapper {
  margin: 1rem;
}
.wrapper .box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.wrapper .box > *:not(:last-child) {
  margin-bottom: 1.81rem;
}
.wrapper .box.rating-submitted {
  align-items: center;
}
.wrapper .hide {
  display: none;
}

.rating .star_img {
  background-color: #1e252c;
  width: 3.125rem;
  height: 3.125rem;
  border-radius: 50%;
  display: grid;
  place-items: center;
}
.rating .star_img img {
  transform: scale(1.4);
}
.rating-question {
  font-size: 1.5625rem;
}
.rating-desp {
  font-size: 0.8125rem;
  color: #7c8798;
}
.rating-numbers {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.rating-btn {
  background-color: #1e252c;
  width: 3.125rem;
  height: 3.125rem;
  color: inherit;
  border: none;
  outline: none;
  border-radius: 50%;
}
.rating-btn:not(:last-child) {
  margin-right: 1rem;
}
.rating-btn:hover {
  cursor: pointer;
  background-color: #fb7413;
  color: white;
}
.rating-btn.clicked {
  background-color: #7c8798;
  color: white;
}
.rating-submit {
  width: 100%;
  background-color: #fb7413;
  text-transform: uppercase;
  color: inherit;
  border: none;
  outline: none;
  border-radius: 1.4rem;
  padding: 1rem;
  font-size: 0.875rem;
}
.rating-submit:hover {
  background-color: white;
  color: #fb7413;
  cursor: pointer;
}

.rating-submitted-card {
  background-color: #1e252c;
  color: #fb7413;
  font-size: 0.875rem;
  padding: 0.5rem 1rem;
  border-radius: 1.2rem;
}
.rating-submitted-response {
  font-weight: 700;
  font-size: 1.3125rem;
}